*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* antd icons */

.switch-icons{
    border: 1px solid rgba(0, 0, 0, 0.6);
    border-radius: 5px;
    padding: 10px 15px;
}
.anticon svg {
    font-size: 20px;
    cursor: pointer;
}

.active-icon{
    color: rgb(10, 0, 0);
}
.inactive-icon{
    color: gray;
}